import React, {useState} from 'react';
import { Button, Table } from 'react-bootstrap';
import Todo from './todo';
import data from '../data/data.json';
import Header from './header';

function App() {
	const initialValue = data;
	const [todos, setTodos] = useState(initialValue);
	const [sortParams, setSortParams] = useState({
		status: null,
		taskName: null,
		priority: null,
		dueDate: null,
	})
	const newTodo = React.createRef()

	function makeComplete(todo) {
		const temp = todos.map(i => i.id === todo.id ? {...i, complete: !i.complete} : i);
		setTodos(temp);
	}

	function markPriority(todo) {
		const temp = todos.map(i => i.id === todo.id ? {...i, priority: 'high'} : i);
		setTodos(temp);
	}

	function clearSelected() {
		const temp = todos.filter(i => !i.complete);
		setTodos(temp);
	}

	function addTodo(e) {
		e.preventDefault();
		const value = newTodo.current.value;
		if (!value) {
			return;
		}
		const temp = [...todos, {
			id: initialValue.length + 1,
			task: value,
			complete: false
		}];
		setTodos(temp);
		newTodo.current.value = null;
	}

	function sort(param) {
		let temp = [];
		switch (param) {
			case 'priority':
				temp = todos.sort((a, b) => b.priority === 'high' ? 0 : -1 );
				break;
			case 'taskName':
				temp = todos.sort((a, b) => b.task[0] > a.task[0] ? 0 : -1 );
				break;
			case 'status':
				temp = todos.sort((a, b) => b.complete ? 0 : -1 );
				break;
			case 'dueDate': 
				temp = [...todos].sort((a, b) => {
					const aDueDate = a.dueDate ? Date.parse(a.dueDate) : 0;
					const bDueDate = b.dueDate ? Date.parse(b.dueDate) : 0;
					return bDueDate - aDueDate;
				});
				break;
		}
		let sort = sortParams[param];
		if (!!sort) {
		 	sort =  sort === 'up' ? 'down' : 'up';
		} else {
			sort = 'up';
		}
		temp = sort === 'up' ? temp : temp.reverse();
		setTodos([...temp]);
		setSortParams({[param]: sort});
	}

	function setDueDate(todo, value) {
		const temp = todos.map(i => i.id === todo.id ? {...i, dueDate: new Date(value).toISOString().substr(0,10)} : i);
		setTodos(temp);
	}

	function doSearch(e, value) {
		e.preventDefault();
		if (!value) {
			setTodos(initialValue);
			return;
		}
		const temp = todos.filter(i => i.task && i.task.toLowerCase().includes(value));
		setTodos(temp);
	}

    return (
  		<div>
  			<Header doSearch={(e, value) => doSearch(e, value)}/>
  			<div className="container">
	  			<Table striped bordered hover>
	  			<thead>
		  			<tr>
		  				<th>Status <i onClick={() => sort('status')} className={`fa-solid fa-sort${!sortParams.status ? '' : sortParams.status === 'up' ? '-up' : '-down'}`}></i></th>
		  				<th>Task <i onClick={() => sort('taskName')} className={`fa-solid fa-sort${!sortParams.taskName ? '' : sortParams.taskName === 'up' ? '-up' : '-down'}`}></i></th>
		  				<th>Priority <i onClick={() => sort('priority')} className={`fa-solid fa-sort${!sortParams.priority ? '' : sortParams.priority === 'up' ? '-up' : '-down'}`}></i></th>
		  				<th>Due Date <i onClick={() => sort('dueDate')} className={`fa-solid fa-sort${!sortParams.dueDate ? '' : sortParams.dueDate === 'up' ? '-up' : '-down'}`}></i></th>
		  			</tr>
	  			</thead>
	  			<tbody>
					{todos.map((todo, index) => 
						<Todo 
							key={`${index}-${todo.id}`}
							todo={todo} 
							makeComplete={() => makeComplete(todo)}
							markPriority={() => markPriority(todo)}
							setDueDate={(value) => setDueDate(todo, value)} 
						/>
					)}
				</tbody>
	  			</Table>
	  			<div className="add-todo">
	  				<form onSubmit={(e) => addTodo(e)}>
	  					<input type="text" ref={newTodo} />
	  					<Button type="submit">Add todo item</Button>
	  					<Button onClick={() => clearSelected()}>Clear Selected</Button>
	  				</form>
	  			</div>
  			</div>
  		</div>
    )
}

export default App;