import React from 'react';
import { Badge } from 'react-bootstrap';

function Todo({todo, makeComplete, markPriority, setDueDate}) {
	const dueDate = React.createRef();

	return (
		<tr>
			<td><input type="checkbox" defaultChecked={todo.complete} onChange={() => makeComplete()} /></td>
			<td><label>{todo.task}</label></td>
			<td>{
				todo.priority === 'high' ? 
				<Badge bg="primary">High priority</Badge> : 
				<Badge bg="secondary" onClick={() => markPriority()}>Mark as High priority</Badge>
			}</td>
			<td>
				<input type="date" value={todo.dueDate ? todo.dueDate : ''} ref={dueDate} onChange={() => setDueDate(dueDate.current.value)} />
			</td>
		</tr>
  	)
}

export default Todo;